import { actions } from '@/util/store'

export default {
    vuex: {
        actions: actions
    },
    data () {
        return {
            content: ''
        };
    },
    methods: {
        onKeyup (e) {
            if (e.ctrlKey && e.keyCode === 13 && this.content.length) {
                // this.sendMessage(this.content);
                actions.sendMessage(this.$store, this.content)
                this.content = '';
            }
        }
    }
};



