
import MyKeyword from './MyKeyword'
import Search from 'vux/src/components/search/index.vue'
import Group from 'vux/src/components/group/index.vue'

import myAjax from '@/util/myAjax'
export default {
name: 'mySearch',
props: {
nowId: {
type: String,
required: true,
}
},
components: {
Search,
MyKeyword,
Group,
},
data() {
return {
searchValue: '',
searched_keyword_data: [],
}
},
methods: {
searchOnFocus(){
$('.searched_content').css('display', 'block')
$('.local_content').css('display', 'none')
},
setFocus() {
this.$refs.search.setFocus()
},
onSubmit() {
this.$refs.search.setBlur()
this.$vux.toast.show({
type: 'text',
position: 'middle',
text: '查询中...'
})
let that = this
myAjax.searchKeyword(function(ret) {
that.searched_keyword_data = ret.searched
if (ret.searched.length < 1) {
that.$vux.toast.hide()
that.$vux.toast.show({
type: 'text',
position: 'middle',
text: 'sorry，未找到'
})
} else {
that.$vux.toast.hide()
}
}, that.searchValue)
},
onCancel() {
$('.searched_content').css('display', 'none')
$('.local_content').css('display', 'block')
this.searched_keyword_data = []
},
}
}



