export default {
    data() {
        return {
        }
    },
    filters: {
        // 将日期过滤为 hour:minutes
        time (date) {
            if (typeof date === 'string') {
                date = new Date(date);
            }
            return date.getHours() + ':' + date.getMinutes();
        }
    },
    watch: {
        session: {
            handler: function (newQuestion, oldQuestion) {
                this.$nextTick(() => {
                    let el = $('.message')[0]
                    el.scrollTop = el.scrollHeight - el.clientHeight;
                });
            },
            deep: true,
        }
    },
};



